import React from 'react';
import {connect} from 'react-redux';
import Companies from './Companies';
import {addComapnyAC, changeNameAC, changeInfoAC} from '../../reducers/companiesReducer';
import axios from 'axios';

class ComapniesAPI extends React.Component{
    componentDidMount(){
        axios.get('http://itsmeapi/api/companies', {withCredentials: true}).then(function(response) {
                console.log(response);
                console.log(response.data)
                if(!response.data.status){
                     console.log("There are not companies");
                } else {
                    console.log(response.data)
                }
            })
    }
    addCompany = () => {
        let formData = new FormData();
        formData.append('name', this.props.state.companies.changedName);
        formData.append('info', this.props.state.companies.changedInfo);
        fetch(`http://itsmeapi/api/company`, {method: 'POST', body: formData}, {withCredentials: true}).then(function(response) {
                console.log(response);
                return response.json();
            }).then(function(body) { 
                console.log(body);
            }); 
    } 
 
    render(){
        return(
            <Companies state={this.props.state}
                       addComapny={this.props.addComapnyAC}
                       changeName={this.props.changeNameAC}
                       changeInfo={this.props.changeInfoAC}
                       addCompany={this.addCompany}
            />
        )
    }
}

let mapStateToProps = (state) => {
    return {
        state: state
    }
}

const ComapniesContainer = connect(mapStateToProps, {addComapnyAC, changeNameAC, changeInfoAC})(ComapniesAPI);
export default ComapniesContainer;